html {
  scroll-behavior: smooth;
}
body {
  height: 100vh;
  background: linear-gradient(
      0deg,
      rgba(255, 255, 255, 0),
      rgb(87, 158, 167) 250%
    )
    no-repeat;
    overflow-x: hidden;
}


/* width */
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar:hover {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(200, 200, 200);
  border-radius: 50px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #424242;
  border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #7c7c7c;
}

.navbar {
  display: flex;
  align-items: center;
}
.logo {
  width: 100px;
  animation: Rotate 20s ease-in infinite;
}
@media (prefers-reduced-motion: no-preference) {
  .logo {
    animation: Rotate infinite 20s linear;
  }
}
@keyframes Rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.tab-menu {
  display: flex;
  list-style-type: none;
}
.item-tab {
  margin-left: 5px;
}
.item-tab a {
  padding: 10px 20px;
  text-decoration: none;
  color: #007;
}
.item-tab a {
  position: relative;
  border-radius: 8px;
}
.item-tab a.active {
  background-color: #0072;
  transform: scaleX(1);
  border-radius: 8px;
}
.item-tab a::before {
  content: "";
  position: absolute;
  right: 25%;
  bottom: 100px;
  width: 50%;
  height: 4px;
  border-radius: 10px;
  background: #007;
  transition: 0.3s ease;
  opacity: 0;
}
.item-tab a.active::before {
  opacity: 1;
  bottom: 0;
}
.login_sing-up {
  margin-right: auto;
}
.container {
  margin: auto;
  width: 95%;
}
.login_sing-up {
  border: none;
  outline: none;
  cursor: pointer;
  background: #654fef;
  padding: 10px;
  border-radius: 8px;
}
.login_sing-up div {
  font-size: 14px;
  color: #eee;
  margin-right: 5px;
  text-decoration: none;
}
.header-app {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.header-app img {
  max-width: 300px;
  position: relative;
  animation: ani 0.8s ease;
}
@keyframes ani {
  from {
    transform: translateY(100px);
  }
  to {
    transform: translateY(0);
  }
}
.header-app .title {
  width: 60%;
  animation: ani1 0.8s ease;
}
@keyframes ani1 {
  from {
    transform: translateX(-100px);
  }
  to {
    transform: translateX(0);
  }
}

.modal {
  position: fixed;
  top: 25%;
  left: 25%;
  width: 50%;
  height: auto;
  z-index: 1066;
  background: rgb(255, 255, 255);
  border-radius: 8px;
}
.modal .container {
  padding: 10px;
}
.modal input {
  width: 100%;
  padding: 8px;
  margin-block: 10px;
  border-radius: 8px;
  border: none;
  background: #eee;
}
.modal input:focus {
  outline: none;
}
.modal button {
  margin-top: 10px;
  background: #654fef;
  border: none;
  outline: none;
  padding: 8px 15px;
  border-radius: 5px;
  width: 100%;
  display: block;
  color: #eee;
  cursor: pointer;
}
.modal2 {
  position: fixed;
  top: 25%;
  left: 100%;
  width: 50%;
  height: auto;
  z-index: 1066;
  background: rgb(255, 255, 255);
  border-radius: 8px;
}
.modal2 .container {
  padding: 10px;
}
.modal2 input {
  width: 100%;
  padding: 8px;
  margin-block: 10px;
  border-radius: 8px;
  border: none;
  background: #eee;
}
.modal2 input:focus {
  outline: none;
}
.modal2 button {
  margin-top: 10px;
  background: #654fef;
  border: none;
  outline: none;
  padding: 8px 15px;
  border-radius: 5px;
  width: 100%;
  display: block;
  color: #eee;
  cursor: pointer;
}
.blur-backdrop {
  display: none;
  transition-delay: 0.3s;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1065;
  background: rgba(0, 0, 0, 0.205);
  backdrop-filter: blur(6px);
}
footer {
  display: block;
  margin-top: 150px;
  background: #204fef;
  color: #eee;
  padding: 40px;
  border-radius: 50px 50px 0 0;
}
.foot {
  width: 33.33%;
  margin-right: 20px;
}
.post-body {
  padding: 50px;
}
.md-block-unstyled {
  margin-top: 20px;
}
.nn {
  padding: 100px;
}

.Contact-Form {
  width: 50%;
  margin: auto;
  padding: 20px;
}
.Contact-Form input {
  background: #eee;
  border: none;
  padding: 10px;
  width: 100%;
  margin-top: 15px;
  border-radius: 8px;
  border-bottom: 4px solid #654fef;
}
.Contact-Form textarea {
  background: #eee;
  border: none;
  padding: 10px;
  width: 100%;
  margin-top: 15px;
  border-radius: 8px;
  border-bottom: 4px solid #654fef;
}
input:focus,
textarea:focus {
  outline: none;
  border-right: 4px solid #654fef;
  border-bottom: 0px solid #654fef;
}
.Contact-Form button {
  width: 100%;
  padding: 10px;
  background: #654fef;
  color: #eee;
  margin-top: 10px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
}
.Contact-Form button:hover {
  color: rgb(0, 0, 0);
  border-bottom: 4px solid #007;
  background: #204fef;
}
@media (max-width: 960px) {
  footer {
    flex-direction: column;
  }
  .foot {
    width: 100%;
  }
}
.prodects {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  animation: showpost 0.5s ease-in-out;
}
.posted {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 5px 5px 20px #bebebe;
  width: calc(20% - 20px);
  margin: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  transform: translateX(0);
  opacity: 1;
  text-decoration: none;
  color: #005;
  transition: 0.3s ease;
  /* transform: translateX(-2000px);
  opacity: 0; */
}
@keyframes showpost {
  from {
    opacity: 0;
    transform:translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.posted.show {
    transform: translateX(0);
    opacity: 1;
}
.posted:hover {
  color: #909;
  transform: translateY(-10px);
  box-shadow: 0px 15px 20px #bebebe;
}
.posted h4 {
  margin-block: 20px;
  font-size: 14px;
}
.posted strong {
  width: 30%;
  display: block;
  background: coral;
  font-size: 20px;
  text-align: center;
  margin-right: auto;
  border-radius: 10px;
}
.posted .title {
  height: 60px;
}
.singelProduct {
  display: flex;
  background: #fff;
  border-radius: 15px;
  box-shadow: 5px 5px 20px #bebebe;
}
.singelProduct .caption {
  padding: 20px;
}
.singelProduct img {
  width: 400px;
}
.Section_news {
